<template>
  <div class="payResult mescroll">
    <div class="top">
      <div style="height:25vh;">
        <div v-if="show">
          <img src="../../assets/img/merchantsIcon/success.png" alt />
          <p class="state">绑定成功</p>
          <p class="text">恭喜你，绑定成功，成为{{shopName}}店的职员</p>
        </div>
      </div>
      <!-- <div class="btns">
        <div class="btn">
          <div class="btnContent" @click="toIndex">去首页</div>
        </div>
        <div class="btn">
          <div class="btnContent" @click="share">去分享</div>
        </div>
      </div> -->
    </div>

  </div>
</template>
<script>
import { addEmployees } from "@/api";
export default {
  data() {
    return {
      show:false,
      shopName:''
    };
  },
  methods: {
    bind() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      addEmployees({
        shopCode: this.$route.query.shopCode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.shopName = res.data;
          this.show = true;
        }else{
          this.$toast.fail(res.errorMsg);
        }
      });
    }
  },
  mounted() {
    document.title = "绑定结果";
    this.bind();
  }
};
</script>
<style lang="scss" scoped>
.payResult {
  background: #f4f4f4;
  .top {
    text-align: center;
    img {
      width: 60px;
      height: 60px;
      margin: 10vh 0 20px;
    }
    .price {
      color: $base-color;
      font-weight: bold;
      font-size: 30px;
      padding: 5px 0;
      span {
        font-size: 18px;
        margin-right: 5px;
      }
    }
    .state {
      font-weight: bold;
      line-height: 30px;
    }
    .text {
      font-size: 13px;
      color: #999;
      line-height: 20px;
      margin: 20px 0 0;
    }
    .orderInfo {
      margin: 50px 0 0;
      line-height: 35px;
      padding: 0 20px;
      font-size: 15px;
      .flex {
        display: flex;
        align-items: center;
        .flex-1 {
          width: 100px;
          color: #999;
          text-align: left;
        }
        .flex-2 {
          flex: 2;
          text-align: right;
        }
      }
    }
    .btns {
      margin: 25vh 8vw 0;
      display: flex;
      .btn {
        flex: 1;
        .btnContent {
          width: 30vw;
          margin: 0 auto;
          background: $base-color;
          border-radius: 5px;
          height: 40px;
          line-height: 40px;
          color: #fff;
          font-size: 15px;
        }
      }
    }
  }
}
</style>
